.product {
  width: 300px;
  padding: 1rem;
  background: #fff;
  border-radius: 2%;
  cursor: pointer;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
  margin: 8px auto;
  display: flex;
  background-color: #ff95df2e;;
  flex-direction: column;
  justify-content: space-between;
  flex-direction: column;
}

.product > img {
  width: 100%;
  
  height: 195px;
  object-fit: revert;
  border-radius: 8px;
}

.product__info > p {
  margin-bottom: 8px;
}

.info__price {
  font-weight: bold;
}

.info__name {
  font-size: 1rem;
  overflow: hidden;
  margin-top: 8px;
}

.info__description {
  font-size: 0.8rem;
}

.info__button {
  display: block;
  text-decoration: none;
  text-align: center;
  color: #171717;
  width: 100%;
  padding: 8px 16px;
  background-color: #f4f4f4;
 

  border: 1px solid #ee1010;
  font-size: 1rem;
  border-radius: 2px;
}

.info__button:hover {
  background: #171717;
  color: #f4f4f4;
  border-radius: 8px;
  background: linear-gradient(50deg, #dbafc9 10%, #6d0050 100%);
}

@media (max-width: 1232px) {
  .product {
    width: 360px;
  }
}

@media (max-width: 1115px) {
  .product {
    width: 330px;
  }
}

@media (max-width: 1028px) {
  .product {
    width: 300px;
  }
}

@media (max-width: 950px) {
