.cartitem {
  width: 100%;
  padding: 1rem;
  display: grid;
  grid-template-columns: 2fr 3fr 1fr 1fr 1fr;
  gap: 8px;
  background: #fff;
  box-shadow: 6.9px 13.8px 13.8px rgba(145, 68, 68, 0.2);
  border-radius: 2px;
  place-items: center;
  margin-bottom: 8px;
} 

/* .cartitem__image{
    padding: 2px;
    box-shadow: rgba(3, 102, 214, 0.3) 0px 0px 0px 3px;
} */

.cartItem__name {
  text-decoration: none;
  color: #171717;
}

.cartItem__name:hover {
  color: #dd219e;
}

.cartItem__select {
  padding: 10px 17px;
}

.cartItem__deleteBtn{
  padding: 10px 17px;
  color: red;
  background: #f4f4f4;
  border: 1px solid #171717;
  cursor: pointer;
  transition: all 0.3s ease-out;
}

.cartItem__deleteBtn:hover,
.carItem__deleteBtn:active,
.carItem__deleteBtn:focus {
  background: #171717;
  transform: scale(1.2);
}

@media (max-width: 700px) {
  .cartItem__name {
    font-size: 0.8rem;
  }

  .cartItem__select,
  .cartItem__deleteBtn {
    padding: 8px 13px;
  }

  .cartitem__price {
    font-size: 0.8rem;
  }
}

@media (max-width: 700px) {
  .cartItem__name {
    font-size: 0.6rem;
  }

  .cartItem__select,
  .cartItem__deleteBtn {
    padding: 5px 8px;
  }

  .cartitem__price {
    font-size: 0.6rem;
  }
}